body {
    background-color: #e6d2bb;
    transition: background-color 0.5 ease;
    overflow: scroll;
    overflow-x: hidden;
    margin: 0;
    padding: 0;
    /*background-image: url(BackgroundImg.png);*/
}

body:hover {
    cursor: url(Cursor.png), auto;
}

.fa-envelope,
.fa-instagram,
.fa-location-dot,
.fa-tiktok,
.fa-linkedin {
    color: #000000;
}

.fa-instagram:hover,
.fa-tiktok:hover,
.fa-linkedin:hover {
    color: gray;
}

label {
    display: inline-block;
    cursor: pointer;
    color: black;
    padding: 0.25em;
    border-radius: 0.25em;
}

.form {
    font-size: 1.5em;
}

#ch,
#ch1,
#ch2,
#ch3,
#ch4,
#ch5,
#ch6,
#ch7,
#ch8,
#ch9,
#ch10,
#ch11,
#ch12,
#ch13,
#ch14,
#ch15,
#ch16,
#ch17 {
    display: none;
}

#ch:checked~.content,
#ch1:checked~.content,
#ch2:checked~.content,
#ch3:checked~.content,
#ch4:checked~.content,
#ch5:checked~.content,
#ch6:checked~.content,
#ch7:checked~.content,
#ch8:checked~.content,
#ch9:checked~.content,
#ch10:checked~.content,
#ch11:checked~.content,
#ch12:checked~.content,
#ch13:checked~.content,
#ch14:checked~.content,
#ch15:checked~.content,
#ch16:checked~.content,
#ch17:checked~.content {
    display: block;
}

#ch:checked~label,
#ch1:checked~label,
#ch2:checked~label,
#ch3:checked~label,
#ch4:checked~label,
#ch5:checked~label,
#ch6:checked~label,
#ch7:checked~label,
#ch8:checked~label,
#ch9:checked~label,
#ch10:checked~label,
#ch11:checked~label,
#ch12:checked~label,
#ch13:checked~label,
#ch14:checked~label,
#ch15:checked~.label,
#ch16:checked~.label,
#ch17:checked~.label {
    display: none;
}

.content {
    display: none;
}

.body-swirl {
    background-image: url(BeigeSwirl.png);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    width: 100vw;
    height: 100vh;
    margin: 0;
    padding: 0;
}

.staff-swirl {
    background-image: url(BeigeSwirl.png);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
}

.home-college-box {
    background-color: #e6d2bb;
    justify-content: center;
    align-items: center;
    justify-content: center;
    vertical-align: middle;
    border-color: #000000;
    border-style: solid;
    object-fit: contain;
}

.college-div {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    display: inline-flex;
}

/*Color-Change Button*/
.color-change-button {
    font-size: 16px;
    cursor: pointer;
    justify-content: center;
    margin: auto;
    font-family: "Fira Sans", sans-serif;
    background-color: #A98F92;
    position: fixed;
    bottom: 0;
    left: 97%;
    transform: translateX(-50%);
}

.submit {
    font-size: 1em;
    background-color: #A98F92;
    color: #DDA8A1;
}


.return-home {
    display: flex;
    align-self: left;
    height: 100%;
    width: auto;
    margin-left: 1em;
    vertical-align: middle;
}

h1,
h2,
h3,
h4,
p,
.name,
.read,
.tab-umich button,
.tab-msu button,
.tab-mtu button,
.tab-cmu button,
.tab-svsu button,
.tab-wsu button,
.tab-wmu button,
.tab-del button,
.tab-mm button,
.tab-jc button,
.tab-kvcc button,
.tab-kc button,
.tab-lcc button {
    font-family: "Fira Sans", sans-serif;
    font-weight: 700;
    font-style: normal;
    text-align: center;
    color: #000000;
    word-wrap: break-word;
    padding: 0.5em;
}

.tab-umich button,
.tab-msu button,
.tab-mtu button,
.tab-cmu button,
.tab-svsu button,
