}

/*LINKS*/
a:hover {
    color: #A98F92;
    background-color: #e6d2bb;
}

a:link,
a.visited,
a:active {
    text-decoration: none;
}

.link-color {
    color: #000000;
}

.transparent {
    background-color: transparent;
}

/*IMAGES*/
img {
    text-align: center;
    justify-content: center;
}

@keyframes bounceScroll {

    0%,
    100% {
        transform: translateX(0);
    }

    50% {
        transform: translateX(-100vw);
    }
}

.scroll {
    white-space: nowrap;
    display: inline-flex;
    overflow: hidden;
    animation: bounceScroll 10s ease-in-out infinite;
}

.fa-instagram,
.fa-envelope,
.fa-facebook,
.fa-location-dot {
    color: #000000;
    margin-right: 1vw;
}

/*COLLEGE BOXES*/
.colleges {
    background-color: #DDA8A1;
    border-radius: 3em;
    vertical-align: middle;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.events {
    background-color: #DDA8A1;
    border-radius: 3em;
    vertical-align: middle;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.college-image {
    display: flex;
    justify-content: center;
    align-items: center;
    vertical-align: middle;
    background-color: none;
}

ul{
    list-style-type: none;
    padding: 0; 
}

/*DROPDOWN*/
.dropdown {
    position: relative;
    display: inline-block;
}

.dropdown-content, .nested-dropdown-content {
    display: none;
    position: relative;
    box-shadow: 0 8px 1em rgba(0, 0, 0, 0.1);
}

.dropdown-content .box {
    display: inline-block;
}

.nested-dropdown:hover .nested-dropdown-content {
    display: block;
}

.dropbtn {
    border: none;
    cursor: pointer;
    background-color: transparent;
    font-family: "Fira Sans", sans-serif;
    font-weight: 700;
    font-style: normal;
    text-align: center;
    color: #000000;
}

.dropbtn:hover {
    color: #A98F92;
    background-color: #e6d2bb;
}

