.modal {
    align-items: center;
    display: flex;
    justify-content: center;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: white;
    transition: all 0.4s;
    visibility: hidden;
    opacity: 0;
}

.modal:target {
    visibility: visible;
    opacity: 1;
}

.box-close {
    position: absolute;
    top: 0;
    right: 15px;
    color: black;
    text-decoration: none;
    font-size: 2em;
}

.slideshow-container {
    position: relative;
    border-width: 1em;
}

.mySlides {
    text-decoration: none;
}

.prev,
.next {
    cursor: pointer;
    position: absolute;
    margin-top: -2em;
    padding: 1em;
    color: black;
    font-weight: bold;
    font-size: 1.5em;
    transition: 0.6s ease;
    border-radius: 0 3px 3px 0;
    user-select: none;
}

.next {
    right: 0;
    border-radius: 3em 0 0 3em;
}

.prev {
    left: 0;
    border-radius: 3em 0 0 3em;
}

.prev:hover {
    color: red;
}

.next:hover {
    color: red;
}

.dot {
    cursor: pointer;
    height: 1em;
    width: 1em;
    margin: 0 2px;
    background-color: black;
    border-radius: 50%;
    display: inline-block;
    transition: background-color 0.6s ease;
}

.active,
.dot:hover {
    background-color: gray;
}

